/* =About Us Section */
.about-banner-container {

    width: 100%;
    margin-bottom: 50px;
}

.about-banner-container img {
    object-fit: contain;
    object-position: center;
    width: 100%;
    border-radius: 20px;

}

.about-section {
    /* background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%); */
    padding: 80px 0;
    position: relative;
    overflow: hidden;
}

.about-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 20% 20%, rgba(255, 138, 0, 0.03) 0%, transparent 50%),
        radial-gradient(circle at 80% 80%, rgba(123, 67, 151, 0.03) 0%, transparent 50%);
    pointer-events: none;
}

.about-container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 20px;
    position: relative;
    z-index: 2;
}

/* Section Header */
.about-header {
    text-align: center;
    margin-bottom: 60px;
}

.about-title {
    font-size: 48px;
    font-weight: 800;
    color: #fff;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.about-title i {
    color: #ff8a00;
    font-size: 54px;
    animation: float 3s ease-in-out infinite;
}

@keyframes float {

    0%,
    100% {
        transform: translateY(0px);
    }

    50% {
        transform: translateY(-10px);
    }
}

.about-subtitle {
    font-size: 20px;
    color: #fff;
    font-weight: 500;
    max-width: 800px;
    margin: 0 auto;
    line-height: 1.6;
}

/* Story Content */
.story-content {
    background: white;
    border-radius: 25px;
    padding: 50px;
    margin-bottom: 80px;
    box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
    position: relative;
    overflow: hidden;
}

.story-content::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background: linear-gradient(135deg, #ff8a00 0%, #e52e71 100%);
}

.story-text {
    font-size: 18px;
    line-height: 1.8;
    color: #444;
    text-align: justify;
    margin-bottom: 30px;
}

.story-text:first-child {
    font-size: 20px;
    font-weight: 600;
    color: #333;
}

.krishna-quote {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    padding: 25px;
    border-radius: 15px;
    font-style: italic;
    font-size: 16px;
    position: relative;
    margin: 30px 0;
}

.krishna-quote::before {
    content: '"';
    font-size: 60px;
    position: absolute;
    top: -10px;
    left: 20px;
    opacity: 0.3;
}

/* Vision Mission Section */
.vision-mission {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    margin-bottom: 80px;
}

.vm-card {
    background: white;
    border-radius: 20px;
    padding: 40px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    cursor: pointer;
}

.vm-icon {
    color: rgb(255, 255, 255);
    font-size: 28px;
    height: 60px;
    justify-content: center;
    width: 60px;
    background: linear-gradient(135deg, rgb(255, 138, 0), rgb(229, 46, 113));
    border-radius: 15px;
}

.vm-icon2 {
    background: linear-gradient(135deg, rgb(102, 126, 234), rgb(118, 75, 162));
}

.vm-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.15);
}

.vm-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    transition: all 0.3s ease;
}

.vision-card::before {
    background: linear-gradient(135deg, #ff8a00 0%, #e52e71 100%);
}

.mission-card::before {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.vm-header {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 20px;
}

.vm-icon {
    width: 60px;
    height: 60px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 28px;
    color: white;
}

.vision-card .vm-icon {
    background: linear-gradient(135deg, #ff8a00 0%, #e52e71 100%);
}

.mission-card .vm-icon {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.vm-title {
    font-size: 28px;
    font-weight: 700;
    color: #333;
}

.vm-content {
    font-size: 16px;
    line-height: 1.6;
    color: #666;
}

.career-highlight {
    background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
    color: white;
    padding: 15px 20px;
    border-radius: 10px;
    margin-top: 15px;
    font-weight: 600;
    text-align: center;
}

/* Services Grid */
.services-preview {
    background: white;
    border-radius: 20px;
    padding: 40px;
    margin-bottom: 80px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.services-title {
    text-align: center;
    font-size: 32px;
    font-weight: 700;
    color: #333;
    margin-bottom: 40px;
}

.services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 25px;
}

.service-item {
    background: #f8f9fa;
    border-radius: 15px;
    padding: 25px;
    text-align: center;
    transition: all 0.3s ease;
    border: 2px solid transparent;
}

.service-item:hover {
    background: white;
    border-color: #ff8a00;
    transform: translateY(-5px);
    box-shadow: 0 10px 25px rgba(255, 138, 0, 0.2);
}

.service-icon {
    width: 50px;
    height: 50px;
    background: linear-gradient(135deg, #ff8a00 0%, #e52e71 100%);
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 15px auto;
    font-size: 20px;
    color: white;
}

.service-name {
    font-size: 16px;
    font-weight: 600;
    color: #333;
}

/* Team Section */
.team-section {
    margin-bottom: 40px;
}

/* .team-title {
    text-align: center;
    font-size: 42px;
    font-weight: 800;
    color: #fff;
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
} */

.team-title i {
    color: #ff8a00;
    font-size: 48px;
}

.team-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
    gap: 40px;
}

.team-member {
    background: white;
    border-radius: 25px;
    padding: 40px;
    text-align: center;
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.1);
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
}

.team-member:hover {
    transform: translateY(-10px);
    box-shadow: 0 25px 60px rgba(0, 0, 0, 0.15);
}

.team-member::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background: linear-gradient(135deg, #ff8a00 0%, #e52e71 100%);
}

.member-photo {
    width: 180px;
    height: 180px;
    border-radius: 50%;
    margin: 0 auto 25px auto;
    overflow: hidden;
    border: 5px solid #ff8a00;
    position: relative;
}

.member-photo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.team-member:hover .member-photo img {
    transform: scale(1.1);
}

.member-name {
    font-size: 24px;
    font-weight: 700;
    color: #333;
    margin-bottom: 8px;
}

.member-position {
    font-size: 16px;
    font-weight: 600;
    color: #ff8a00;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.member-bio {
    font-size: 15px;
    line-height: 1.6;
    color: #666;
    text-align: left;
}

/* Statistics Section */
.stats-section {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border-radius: 25px;
    padding: 50px;
    margin-bottom: 40px;
    position: relative;
    overflow: hidden;
}

/* Fix text readability in stats */
.stat-number {
    font-size: 36px;
    font-weight: 800;
    margin-bottom: 10px;
    color: #4a148c;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.stat-label {
    font-size: 16px;
    font-weight: 600;
    color: #6a1b9a;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.stats-section::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.1), transparent);
    transform: rotate(45deg);
    animation: shine 4s infinite;
}

@keyframes shine {
    0% {
        transform: translateX(-100%) rotate(45deg);
    }

    100% {
        transform: translateX(100%) rotate(45deg);
    }
}

.stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 30px;
    position: relative;
    z-index: 2;
}

.stat-item {
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 30px;
    backdrop-filter: blur(10px);
    background: rgba(255, 255, 255, 0.95);
    padding: 15px 20px;
    border-radius: 15px;
    border-width: 2px;
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.3);
    border-image: initial;
}

/* Values section with Hindi words */
.values-section {
    background: linear-gradient(135deg, #ff8a00 0%, #e52e71 100%);
    padding: 40px;
    margin-bottom: 60px;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
}

.values-section::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.1), transparent);
    transform: rotate(45deg);
    animation: shine 6s infinite;
}

.values-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 30px;
    position: relative;
    z-index: 2;
}

.value-item {
    text-align: center;
    color: white;
    padding: 20px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    transition: all 0.3s ease;
}

.value-item:hover {
    background: linear-gradient(135deg, #7b4397 0%, #ff8a00 100%);
    transform: translateY(-8px) scale(1.05);
    box-shadow: 0 20px 40px rgba(123, 67, 151, 0.4);
}

.value-hindi {
    font-size: 24px;
    font-weight: 800;
    margin-bottom: 8px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    font-family: 'Devanagari Sangam MN', 'Noto Sans Devanagari', serif;
}

.value-english {
    font-size: 14px;
    font-weight: 500;
    opacity: 0.9;
    text-transform: uppercase;
    letter-spacing: 1px;
}

/* Responsive Design */
@media (max-width: 1024px) {
    .vision-mission {
        grid-template-columns: 1fr;
        gap: 30px;

        @media (max-width: 768px) {
            .values-grid {
                grid-template-columns: repeat(2, 1fr);
                gap: 15px;
            }

            .value-hindi {
                font-size: 20px;
            }

            .value-english {
                font-size: 12px;
            }
        }

        .team-grid {
            grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
            gap: 30px;
        }
    }

    @media (max-width: 768px) {
        .about-section {
            padding: 60px 0;
        }

        .about-title {
            font-size: 36px;
            flex-direction: column;
            gap: 15px;
        }

        .story-content {
            padding: 30px;
        }

        .story-text {
            font-size: 16px;
        }

        .vm-card {
            padding: 30px;
        }

        .team-member {
            padding: 30px;
        }

        .stats-section {
            padding: 40px 30px;
        }

        .stats-grid {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }

        .stat-number {
            font-size: 28px;
        }
    }

    @media (max-width: 480px) {
        .values-grid {
            grid-template-columns: 1fr;
            gap: 12px;
        }

        .values-section {
            padding: 30px 20px;
        }
    }

    .about-container {
        padding: 0 15px;
    }

    .about-title {
        font-size: 28px;
    }

    .story-content {
        padding: 25px;
    }

    .team-grid {
        grid-template-columns: 1fr;
    }

    .stats-grid {
        grid-template-columns: 1fr;
    }
}

/* Animations */
.team-member,
.vm-card,
.service-item {
    animation: fadeInUp 0.6s ease forwards;
    opacity: 0;
    transform: translateY(30px);
}

.team-member:nth-child(1) {
    animation-delay: 0.1s;
}

.team-member:nth-child(2) {
    animation-delay: 0.2s;
}

.team-member:nth-child(3) {
    animation-delay: 0.3s;
}

@keyframes fadeInUp {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Add icons and colors for each value using Udaan360 logo colors */
.value-item {
    text-align: center;
    color: white;
    padding: 25px 15px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    transition: all 0.3s ease;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

/* Add icon container */
.value-icon {
    font-size: 40px;
    margin-bottom: 15px;
    transition: all 0.3s ease;
    opacity: 0.8;
}

/* Individual color themes using logo colors - Purple and Orange variations */
.value-item:nth-child(1):hover {
    background: linear-gradient(135deg, #7b4397 0%, #9b59b6 100%);
    /* Purple gradient */
    transform: translateY(-8px) scale(1.05);
    box-shadow: 0 20px 40px rgba(123, 67, 151, 0.4);
}

.value-item:nth-child(2):hover {
    background: linear-gradient(135deg, #ff8a00 0%, #ff6b35 100%);
    /* Orange gradient */
    transform: translateY(-8px) scale(1.05);
    box-shadow: 0 20px 40px rgba(255, 138, 0, 0.4);
}

.value-item:nth-child(3):hover {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    /* Purple-blue gradient */
    transform: translateY(-8px) scale(1.05);
    box-shadow: 0 20px 40px rgba(102, 126, 234, 0.4);
}

.value-item:nth-child(4):hover {
    background: linear-gradient(135deg, #e52e71 0%, #ff8a00 100%);
    /* Pink-orange gradient */
    transform: translateY(-8px) scale(1.05);
    box-shadow: 0 20px 40px rgba(229, 46, 113, 0.4);
}

.value-item:nth-child(5):hover {
    background: linear-gradient(135deg, #8e44ad 0%, #3498db 100%);
    /* Purple-blue gradient */
    transform: translateY(-8px) scale(1.05);
    box-shadow: 0 20px 40px rgba(142, 68, 173, 0.4);
}

/* Icon hover effects */
.value-item:hover .value-icon {
    transform: scale(1.2) rotate(10deg);
    opacity: 1;
    text-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

/* Pulse animation for icons */
@keyframes iconPulse {

    0%,
    100% {
        transform: scale(1.2) rotate(10deg);
    }

    50% {
        transform: scale(1.3) rotate(10deg);
    }
}

.value-item:hover .value-icon {
    animation: iconPulse 1.5s infinite;
}

/* Alternative: If you want all cards to use the same logo gradient */
.value-item:hover.logo-theme {
    background: linear-gradient(135deg, #7b4397 0%, #ff8a00 100%);
    /* Direct logo colors */
    transform: translateY(-8px) scale(1.05);
    box-shadow: 0 20px 40px rgba(123, 67, 151, 0.4);
}

/* Simple About Banner */
.about-banner {
    width: 100%;
    height: 400px;
    background: url('https://www.udaan360.com/static/media/About0002.c49bc4b800322fd393ff.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 20px;
    margin-bottom: 40px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

/* Responsive Design */
@media (max-width: 768px) {
    .about-banner {
        height: 250px;
        border-radius: 15px;
        margin-bottom: 30px;
    }
}

@media (max-width: 480px) {
    .about-banner {
        height: 200px;
        border-radius: 10px;
        margin-bottom: 20px;
    }
}

/* Mobile Responsive Fixes for Values Section */
@media (max-width: 768px) {
    .values-section {
        padding: 30px 15px;
        margin: 40px 0;
    }

    .values-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;
        max-width: 100%;
    }

    .value-item {
        padding: 20px 10px;
        min-height: 120px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .value-icon {
        font-size: 28px;
        margin-bottom: 10px;
    }

    .value-hindi {
        font-size: 20px;
        margin-bottom: 8px;
        line-height: 1.1;
    }

    .value-english {
        font-size: 11px;
        letter-spacing: 0.5px;
    }
}

@media (max-width: 480px) {
    .values-section {
        padding: 25px 10px;
        margin: 30px 0;
        border-radius: 15px;
    }

    .values-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 12px;
    }

    .value-item {
        padding: 18px 8px;
        min-height: 110px;
        border-radius: 12px;
    }

    .value-icon {
        font-size: 24px;
        margin-bottom: 8px;
    }

    .value-hindi {
        font-size: 18px;
        margin-bottom: 6px;
        font-weight: 700;
    }

    .value-english {
        font-size: 10px;
        font-weight: 500;
    }
}

/* Extra small screens */
@media (max-width: 360px) {
    .values-grid {
        gap: 10px;
    }

    .value-item {
        padding: 15px 6px;
        min-height: 100px;
    }

    .value-icon {
        font-size: 22px;
        margin-bottom: 6px;
    }

    .value-hindi {
        font-size: 16px;
        margin-bottom: 5px;
    }

    .value-english {
        font-size: 9px;
    }
}

/* Landscape mobile orientation */
@media (max-width: 768px) and (orientation: landscape) {
    .values-section {
        padding: 20px 15px;
    }

    .values-grid {
        grid-template-columns: repeat(5, 1fr);
        gap: 10px;
    }

    .value-item {
        padding: 15px 8px;
        min-height: 90px;
    }

    .value-icon {
        font-size: 20px;
        margin-bottom: 5px;
    }

    .value-hindi {
        font-size: 14px;
        margin-bottom: 3px;
    }

    .value-english {
        font-size: 8px;
    }
}

/* Fix container overflow on mobile */
@media (max-width: 768px) {
    .values-section {
        overflow-x: hidden;
        width: 100%;
        box-sizing: border-box;
    }

    .values-grid {
        width: 100%;
        box-sizing: border-box;
    }

    .value-item {
        box-sizing: border-box;
        width: 100%;
    }
}